
const throttle = (
      fn: () => void,
      delay: number,
      atleast: number) => {
  //  scroll 
  let timeout: any = null;
  let startTime: any = new Date();
  return () => {
    const curTime: any = new Date();
    clearTimeout(timeout);
    if ((curTime - startTime) >= atleast) {
      fn();
      startTime = curTime;
    } else {
      timeout = setTimeout(fn, delay);
    }
  };
};

export { throttle };

